h1 {
  font-size: 28px;
  line-height: 32px;
  margin: 10px 0;
}
h5 {
  margin: 0;
}
a {
  text-decoration: none;
  color: #00a19c;
  transition: all .3s ease;
  cursor: pointer;
}
.hero {
  text-align: center;
}
.clearfix {
  display: block;
}
.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}
.col {
  position: relative;
  float: left;
  width: 100%;
  box-sizing: border-box;
}
.col.col-2 {
  width: 50%;
}
.col.col-3 {
  width: 33.3333%;
}
.col.col-4 {
  width: 25%;
}
.col.gutter {
  padding: 0 .75vw;
  margin-bottom: 1.5vw;
  display: -ms-flexbox;
  display: flex;
}
.lube-recommender-wrapper {
  margin-top: 80px;
}
.lube-recommender-wrapper.lube-recommender-overview .content-container {
  display: block;
}
.lube-recommender-wrapper.lube-recommender-overview .content-container .col-3 {
  height: 80%;
}
.lube-recommender-wrapper.lube-recommender-overview .lube-recommender-block {
  padding: 4vw 0 3.75vw;
}
.lube-recommender-wrapper.lube-recommender-overview .lube-recommender-block h5 {
  text-transform: uppercase;
  margin-top: 30px;
  font-size: 14px;
}
.lube-recommender-wrapper.lube-recommender-overview .lube-recommender-block .image-block {
  min-height: 92px;
}
.lube-recommender-block {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  cursor: pointer;
  font-size: 18px;
}
.lube-recommender-block .index {
  border-radius: 50%;
  background-color: #fff;
  color: #00a19c;
  border: 4px solid #33d4c5;
  width: 22px;
  height: 22px;
  padding: 10px;
  font-weight: 800;
  margin: -20px auto -10px;
}
.lube-recommender-block .inner {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  align-content: center;
  align-items: center;
  height: 15vw;
  padding: 1vw;
  font-weight: 300;
}
.lube-recommender-block .inner img {
  padding-bottom: 20px;
}
.block {
  position: relative;
  display: block;
  background-color: $white;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  border-radius: 2px;
  min-height: 100px;
  width: 100%;
  transition: all .3s ease;
  box-sizing: border-box;
}
.block:hover {
  box-shadow: 0 6px 6px 0 rgba(0, 0, 0, 0.26), 0 10px 20px 0 rgba(0, 0, 0, 0.19);
}
.lube-recommender-tool-wrapper {
  width: 100%;
  height: auto;
  min-height: calc(38vh);
  padding: 3.75vw;
  z-index: 2;
  box-sizing: border-box;
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: cover;
}
.lube-recommender-tool-wrapper .progress {
  color: #00A19C;
  font-size: 12px;
  font-weight: 900;
}
.lube-recommender-tool-wrapper .progress-bar {
  width: 120px;
  display: inline-block;
  margin: 0 10px 2px;
  height: 3px;
  background-color: #eee;
  border-radius: 2px;
}
.lube-recommender-tool-wrapper .progress-bar .progress {
  height: 100%;
  border-radius: 2px;
  background-color: #00a19c;
}
.lube-recommender-tool-wrapper h1 {
  color: black;
  font-weight: 700;
  font-size: 24px;
}
.lube-recommender-tool-wrapper .top-heading h1 {
  font-weight: 800;
  font-size: 26px;
}
.lube-recommender-tool-wrapper .top-heading h4 {
  color: #00A19C;
  margin-bottom: 20px;
  font-size: 16px;
}
.lube-recommender-tool-wrapper .flex-wrapper {
  display: flex;
}
.lube-recommender-tool-wrapper .white-bg {
  position: relative;
  display: block;
  background-color: #fff;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  border-radius: 2px;
  min-height: 100px;
  width: 100%;
  transition: all .3s ease;
  box-sizing: border-box;
  padding: 3.75vw;
  height: auto;
  min-height: calc(30.5vh);
}
.lube-recommender-tool-wrapper .back {
  position: relative;
  display: block;
  margin-bottom: 5px;
  font-size: 14px;
  line-height: 16px;
  font-weight: 700;
  text-transform: uppercase;
}
.lube-recommender-tool-wrapper .back .back-arrow {
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
  font-size: .9em;
  top: -1px;
  margin-right: 3px;
}
.lube-recommender-tool-wrapper .back .forward-arrow {
  font-size: .9em;
  top: -1px;
  margin-left: 3px;
}
.lube-recommender-tool-wrapper .text-center {
  text-align: center;
}
.lube-recommender-tool-wrapper .text-right {
  text-align: right;
}
.lube-recommender-tool-wrapper .lube-recommender-block .inner {
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  align-content: center;
  align-items: center;
  height: 15vw;
  padding: 1vw;
  font-weight: 300;
}
.lube-recommender-tool-wrapper .lube-recommender-block .inner img {
  padding-bottom: 20px;
}
.lube-recommender-tool-wrapper .lube-recommender-block .index {
  border-radius: 50%;
  background-color: white;
  color: #00A19C;
  border: 4px solid #33D4C5;
  width: 22px;
  height: 22px;
  padding: 10px;
  font-weight: 800;
  margin: -20px auto -10px auto;
}
.lube-recommender-tool-wrapper .lube-recommender-block .index.selected {
  background-color: #1ABBB6;
  color: white;
}
.lube-recommender-tool-wrapper ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.lube-recommender-tool-wrapper .temp-block {
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 1px 3px 0 rgba(0, 0, 0, 0.1);
}
.lube-recommender-tool-wrapper .temp-block ul li {
  padding: 0.8vw 1.5vw;
  font-size: .85em;
}
.lube-recommender-tool-wrapper .temp-block ul li:hover {
  background-color: #eee;
  cursor: pointer;
}
.lube-recommender-tool-wrapper .top-wrapper .col {
  width: 20%;
}
.lube-recommender-tool-wrapper .top-wrapper .col.col-main {
  width: 60%;
}
.temp-tool hr {
  margin: 5px 0 0;
}
.temp-tool .temp-head {
  padding: 20px 0 10px;
  color: #3D3935;
  border-bottom: 1px solid #ccc;
}
.temp-tool .temp-head.temp-type {
  margin-bottom: 20px;
}
.temp-tool .temp-head.dropdown {
  padding-right: 25px;
  appearance: none;
  cursor: pointer;
}
.button-group > .button:first-child {
  margin-left: 0;
}
.button-group.right {
  float: right;
  margin-bottom: 5px;
}
.button.white {
  background-color: #fff;
  color: #00A19C;
}
.button-group > .button {
  padding: 0.45rem 1rem;
  font-size: 0.9rem;
  line-height: 1.5;
}
.flex-top-container {
  display: flex;
  flex-direction: column;
  height: 90%;
  justify-content: center;
  padding-bottom: 50px;
}
.flex-container {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-content: stretch;
  align-items: center;
}
.flex-container.small-padding {
  padding-top: 20px;
}

@keyframes spinner {
to {
    transform: rotate(360deg);
}
}
.product-page .ul {
  list-style: none;
}
.product-page .spinner-container {
  position: relative;
  display: block;
  margin: 50px;
  transition: all .3s ease;
}
.product-page .spinner-container .spinner:before {
  content: '';
  box-sizing: border-box;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  margin-top: -10px;
  margin-left: -10px;
  border-radius: 50%;
  border: 2px solid #8DDADA;
  border-top-color: #00A19C;
  animation: spinner .6s linear infinite;
}
.product-page .block:hover {
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 1px 3px 0 rgba(0, 0, 0, 0.1);
}
.product-page .top-container {
  text-align: center;
  padding: 20px;
  border-bottom: 6px solid #006D68;
}
.product-page .top-container .petronas-header {
  text-transform: uppercase;
  color: #00A19C;
  font-size: 12px;
  font-weight: 800;
}
.product-page .top-container h1 {
  font-weight: 400;
}
.product-page .top-container h5 {
  font-size: 14px;
}
.product-page .top-container.standard {
  border-color: #006D68;
}
.product-page .top-container.premium {
  border-color: #A4A7AA;
}
.product-page .top-container.supreme {
  border-color: #C49F05;
}
.product-page .col-product {
  width: 60%;
  max-width: 700px;
}
.product-page .top-header {
  font-size: 14px;
  padding: 10px;
  border-bottom: 1px solid #ddd;
  margin-bottom: 10px;
  font-weight: 700;
}
.product-page .flex-container {
  height: auto;
  min-height: auto;
}
.product-page .bottom-container {
  background-color: #fafafa;
}
.product-page .bottom-container .col {
  padding: 30px 40px;
}
.product-page .bottom-container .footnote {
  margin-top: 20px;
  margin-bottom: -20px;
  padding-left: 30px;
}
.product-page .bottom-container .footnote p {
  font-size: 11px;
  color: #ccc;
  line-height: 1.4;
}
.product-page .oem-manual {
  color: #00A19C;
  font-size: 12px;
  margin-top: 30px;
}
.product-page .oem-manual .bell-icon {
  display: inline-block;
  width: 18px;
  height: 18px;
  margin-bottom: -3px;
  margin-right: 8px;
}
.tick-list {
  padding: 0;
}
.tick-list li {
  padding: 10px 0;
  padding-left: 30px;
}
.tick-list li:before {
  font-family: "icomoon";
  content: "\E91D";
  display: inline-block;
  margin-left: -30px;
  width: 30px;
  color: #1ABBB6;
  font-size: 20px;
}
.document-list {
  padding-top: 5px;
}
.document-list li {
@include shadow-1;
  margin: 10px 0;
  background-color: $white;
}
.document-list li a {
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-content: stretch;
  align-items: center;
  color: #333;
}
.document-list li a :nth-child(2) {
  padding-left: 25px;
}
.document-list li:hover {
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 1px 3px 0 rgba(0, 0, 0, 0.1);
}
.document-list li img {
  height: 75px;
  width: 75px;
  vertical-align: middle;
}
